
.profileBox {
    margin:0 0 0 30px;
    border: 2px solid #ccc;
    border-radius: 5px;
    width: max-content;
    padding: 5px 45px 5px 15px;
    @media (max-width: 600px) {
        padding: 2px 0 0 0;
        width: 90%;
        margin: 0 auto;
        // height: 80%;
    }
    .profileBoxButtonEdit {
        margin: 5px 10px 5px 70%;
    }
.title {
    font-size: 40px;
    margin: 0;
    color: #1976d2;
    // padding: 10px 0 0 30px;
}
.checkBox {
    font-size: 25px;
    padding: 10px 0 0 30px;
    color: #062a4e;
}


.inputs {
    margin: 30px 0 0 60px;
    .inputsButton {
        margin: 0 0 0 100px;
        font-size: 24px;
    }
}
}
.error {
    font-weight: 600;
    color: red;
    padding: 15px;
    text-transform: uppercase;
}